.principalTitle {
	text-align: center;
	margin: 20px 50px;
	color: var(--black);
}
.main-about{
	position: relative;
	margin-top: 100px;
}
.main-image-center{
	width: 100%;
	height: 600px;
}
.main__about-title, .main__about-subtitle{
	position: absolute;
	justify-content: center;
	font-family: var(--fontBase2);
	color: #ffffff;		
}
.main__about-title{
	top: 5px;
	font-size: 45px;
	border-bottom: solid var(--darkOrange);
	border-bottom-width: 5px;
	text-shadow: -2.5px 2.5px 0 var(--darkOrange);
	letter-spacing: 0.3rem;
	background-color: transparent;
	width: 100%;
	text-align: center;
}
.main__about-subtitle{
	top: 170px;
	font-size: 20px;
	background-color: var(--orange);
	background-color: rgba(255, 0, 0, 0.4);
	width: 100%;
	padding-left: 30px;
	padding-right: 30px;
	padding-top: 60px;
	height: 300px;
	border-radius: 10px;	
}
