.grettings{
    height: 15vh;
    display: grid;
    place-items: center;
    padding-top: 30px;
}
.grettings_typing-effect{
    width: 30ch;
    font: var(--headline5); 
	color: var(--black); 
    -webkit-animation: typing 1s steps(12), blink 15s step-end infinite alternate; 
            animation: typing 1s steps(12), blink 15s step-end infinite alternate;
    white-space: nowrap;
    overflow: hidden;
    border-right: 3px solid var(--gradient2);
    -webkit-text-decoration-line: none;
            text-decoration-line: none;
    font-size: 25px;
    text-align-last: center;
}
@-webkit-keyframes typing {
    from{
        width: 0;
    }
}
@keyframes typing {
    from{
        width: 0;
    }
}
@-webkit-keyframes blink {
    50%{
        border-color: transparent;
    }
}
@keyframes blink {
    50%{
        border-color: transparent;
    }
}

/*RESPONSIVE*/

@media screen and (min-width: 320px) and (max-width: 600px){
    .grettings_typing-effect{ 
        font-size: 18px;
    }
}


.containerInput{
    display: flex;
    margin-top: 50px;
}
.form-title, .form-button {
	background: var(--gradient);
	text-align: center;
	color: var(--white);	
	-webkit-padding-before: 4px;	
	        padding-block-start: 4px;
	-webkit-padding-after: 8px;
	        padding-block-end: 8px;
	-webkit-padding-end: 8px;
	        padding-inline-end: 8px;
	-webkit-padding-start:8px;
	        padding-inline-start:8px;
	text-decoration: none;
	margin-top: 25px;
}
.form-title{
	font:var(--headline5);
	box-shadow: -4px 4px 0 var(--white);
}
.form-button{
	font:var(--headline7);
	display: flex;
    margin: -20px -50px;
    position: relative;
    align-items: center;
    margin-inline: auto;
	border-radius: 5px;
	width: 50%;
	justify-content: center;
}
.form-subtitle{
	font:var(--body1);
	text-align: center;
	padding-top: 16px;
}
#sendMsg{
	opacity: 0;
}


.form{
    flex-direction: column;
    grid-gap: 16px;
    gap: 16px;
    margin-top: 50px;
}
.form label{
    display:flex;
    flex-direction: column;
    font: var(--button);
    color: var(--purple);
     margin-top: 24px;
}
.form input, .form textarea{
    border: 2px solid #858C94;
    border-radius: 8px;
    -webkit-padding-end: 16px;
            padding-inline-end: 16px;
    -webkit-padding-start: 16px;
            padding-inline-start: 16px;
    -webkit-padding-after: 16px;
            padding-block-end: 16px;
    -webkit-padding-before: 16px;
            padding-block-start: 16px;
    font: var(--body1);
    -webkit-margin-before: 8px;
            margin-block-start: 8px;
}


@media screen and (min-width: 320px) and (max-width: 599px){
	.form input, .form textarea{
		-webkit-padding-end: 4px;
		        padding-inline-end: 4px;
		-webkit-padding-start: 4px;
		        padding-inline-start: 4px;
		-webkit-padding-after: 4px;
		        padding-block-end: 4px;
		-webkit-padding-before: 4px;
		        padding-block-start: 4px;
		-webkit-margin-before: 4px;
		        margin-block-start: 4px;
		margin-top: 0px;
}
.form label{
        margin-top: 20px;
}

}
