:root{
	--fontBase: 'Poppins', sans-serif;
	--fontBase2: 'Roboto Slab', serif;
	--headline1: bold 95px/153px var(--fontBase);
	--headline2: bold 59px/95px var(--fontBase);
	--headline3: bold 48px/77px var(--fontBase);
	--headline4: bold 34px/55px var(--fontBase);
	--headline5: bold 24px/38px var(--fontBase);
	--headline6: bold 18px/32px var(--fontBase);
	--headline7: bold 14px/28px var(--fontBase);
	--body1: 16px/25px var(--fontBase);
	--body2: 14px/22px var(--fontBase);
	--body3: 20px/31px var(--fontBase);
	--button: bold 14px/25px var(--fontBase);
	--caption: 12px/25px var(--fontBase);
	/* --purple: #310A90; */
	/* --purple: #AF87CE; */
	--purple: #095F8B;
	--black: #000000;
	--green: #42E2B8;
	--white: #ffffff;
	--orange: #F37335; 
	--darkOrange: #dc2f02;
	--gradient: radial-gradient(41.48% 100.48% at 2.38% 8.52%, #FDC830 0%, rgba(255, 255, 255, 0) 100%), linear-gradient(135deg, #F37335 0%, #000000 100%);
	--gradient2: radial-gradient(41.48% 200.48% at 2.38% 8.52%, #000000 0%, rgba(255, 255, 255, 0) 100%), linear-gradient(135deg, #FDC830 0%, #F37335 100%);
	
}

