.banner-menu__ppal {
	position: flex;
	background:#000000;
	background:var(--black);
}
.banner-menu__ppal, .logo2, .logo3, .logo4{
	width: 100%;
	height: 170px;
}

.banner__logo-img, .logo-img2, .logo-img3, .logo-img4{
	height: 150px;
	margin-top: 10px;
}
.logo2, .logo3, .logo4{
	margin-top: 30px;
	background: radial-gradient(41.48% 100.48% at 2.38% 8.52%, #FDC830 0%, rgba(255, 255, 255, 0) 100%), linear-gradient(135deg, #F37335 0%, #000000 100%);
	background: var(--gradient);
}
.logo4{
	background: radial-gradient(41.48% 200.48% at 2.38% 8.52%, #000000 0%, rgba(255, 255, 255, 0) 100%), linear-gradient(135deg, #FDC830 0%, #F37335 100%);
	background: var(--gradient2);
}
.banner-menu__ppal, .logo2, .logo3, .logo4 {
	display: flex;
	justify-content: space-around;	
}
.banner-menu__ppal a, .logo2 a, .logo3 a, .logo4 a{
	margin-top: 80px;
	text-decoration: none;
	font-size: 14px;
}
.menu{
	color: #ffffff;
}
a:hover{
	color: #dc2f02;
	color: var(--darkOrange);
}
.footer{
	display: flex;
	background: radial-gradient(41.48% 200.48% at 2.38% 8.52%, #000000 0%, rgba(255, 255, 255, 0) 100%), linear-gradient(135deg, #FDC830 0%, #F37335 100%);
	background: var(--gradient2);
	width: 100%;
	margin-top: 20%;
	height: 100px;
}
.footer__logo-img2{
	width: 100px;
	height: 80px;
	padding-left: 40px;
	padding-top: 10px;
}
.footer__text, .footer__text2{
	color: #FFFFFF;
	font-size: 15px;
	padding-left: 100px;
	padding-top: 20px;
}
.footer__icon-instagram, .footer__icon-phone, .footer__icon-mail{
	width: 50px;
	height: 50px;
	padding-left: 130px;
	padding-top: 30px;
}

